<template>
<div id="admin">
  <v-container style="position:relative;">
    <v-form ref="form" v-model="valid" lazy-validation>
    <v-text-field v-model="title" :rules="nameRules" label="Titel" required></v-text-field>
    <v-text-field 
      v-model="item.text" 
      placeholder="Hier eine Beschreibung anfügen" 
      v-for="(item,index) in description" 
      v-bind:key="item.id" 
      :label="'Beschreibung Nr.'+(index+1)"  
      :append-icon="(index != 0) ? 'clear' : ''"
      :append-outer-icon="(index==description.length-1) ? 'add' : ''"
      @click:append="sliceThis(index)"
      @click:append-outer="addDescription"
      required>
    </v-text-field>
    <v-text-field v-model="src" label="Link zum Bild" placeholder="https://www.example.com/bild.jpg" required></v-text-field>
    <v-select v-model="select" placeholder="z.B. Pressendrucke" :items="categories" :rules="[v => !!v || 'Bitte geben sie eine Kategorie an']" label="Kategorie" required></v-select>
    <v-btn class="grey lighten-2" absolute right flat>Absenden</v-btn>
    </v-form>
  </v-container>
  <v-container>
    <v-carousel light :cycle="false" hide-delimiters>
      <v-carousel-item>
       <v-layout justify-center align-center>
        <BookCard v-bind:item="obj" :key="obj.id" :routeAhead="false"/>
        </v-layout>
      </v-carousel-item>
      <v-carousel-item>
        <DetailComp v-bind:item="obj" :key="obj.id"/>
      </v-carousel-item>
    </v-carousel>

  </v-container>
</div>
</template>

<script>
import BookCard from '@/components/BookCard.vue'
import DetailComp from '@/components/DetailComp.vue'

  export default {
    components: {
    BookCard,
    DetailComp
    },
    data: () => ({
      description: [{text: '', id:0}],
      valid: true,
      title: '',
      src: '',
      price: '',
      nameRules: [
        v => !!v || 'Bitte geben sie einen Titel an',
      ],
      select: null,
      categories: [
        'Pressendrucke',
        'Einblattdrucke',
        'Sonderdrucke',
        'Graphikeditionen',
        'Hörbücher'
      ]
  }),
  computed:{ 
    obj () {
      return {
        id: 0,
        title: this.title,
        src: this.src,
        description: this.descArr,
        flex: 8,
        price: this.price
      }
    },
    descArr () {
      let arr = []
      this.description.forEach(element => {
        arr.push(element.text)
      })
      return arr
    } 
  },

    methods: {
      validate () {
        if (this.$refs.form.validate()) {
          this.snackbar = true
        }
      },
      reset () {
        this.$refs.form.reset()
      },
      addDescription () {
        this.description.push({text:'', id: this.description.length});
      },
      sliceThis(i) {
        this.description.splice(i,1);
      }
    }
  }
</script>
